.match__li{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    background-color: #fff;
    width: 27vw;
    height: 13vh;
    border: 1px solid #d1d1d1;
    box-shadow: 6px 5px 12px -5px rgba(0,0,0,0.45);
    border-radius: 15px;
    padding: 0.7em;
}
.date {
    width: 100%;
    text-align: center;
    font-size: 0.9rem;
    color: #393E46;
}
.month_date {
    margin-right: 0.5rem;
}
.homeTeam_box, .awayTeam_box {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    flex-grow: 1;
    text-align: center;
    font-size: 1.1rem;
    font-weight: 700;
    flex-basis: 40%;
}
.homTeam_logo, .awayTeam_logo {
    display: block;
    width: 2.5rem;
    height: 2.5rem;
}
.homeTeam_name, .awayTeam_name {
    font-size: 0.8rem;
    margin-top: 0.6rem;
}
.match__li > span{
    margin: 0 1rem;
    font-size: 0.8rem;
    font-style: italic;
}

@media screen and (max-width:850px){
    .match__li {
        width: 55vw;
    }
}