.entrance_wrapper {
    display: flex;
    align-items: center;
    width: 100vw;
    height: 100vh;
    background-image: url("https://images.unsplash.com/photo-1518605254226-2ac70b6b9ea6?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1189&q=80");
    background-position: center;
    background-size: cover;
}
.main_box {
    width: 100%;
    height: fit-content;
}

.entrance_main {
    display: flex;
    flex-wrap: wrap;
}
.entrance_main > h2 {
    font-family:serif;
    font-weight: 900;
    text-align: center;
    width: 100%;
    display: block;
    font-size: 7rem;
    margin: 0;
    color: #F5F5F5;
}
.entrance_main > p {
    text-align: center;
    display: block;
    width: 100%;
    font-size: 1.5rem;
    font-weight: 700;
    margin: 2rem 0 0 0;
    color: #F5F5F5;
}
.button_box {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 15vh;
}
.link {
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    border-radius: 15px;
    width: 8rem;
    height: 4.2rem;
    font-size: 1.6rem;
    font-weight: 700;
    background-color: #FFAA4C;
    color: #fff;
    margin-top: auto;
    transition: .2s;
}

.link:hover{
    border-radius: 100%;
    background-image: url("https://www.pngall.com/wp-content/uploads/4/Premier-League-PNG-Image.png");
    background-position: center;
    background-size: cover;
    width: 6rem;
    height: 6rem;
    color: transparent;
    transform: rotate(360deg);
}